<template>
  <div v-if="editable" class="w-100 overflow-hidden bg-light">
    <div class="top-space"></div>
      <v-carousel v-model="sliderModel" height="160" light :show-arrows="false" touchless hide-delimiters>
        <v-carousel-item
          v-for="(step, i) in steps"
          :key="i"
        >
          <section class="w-100 relative items-center h-100 ">
            <div class="flex h-100 items-center section-wrap center ph3 w-100 w-90-l relative">
              <div class="absolute slide-back">
                <v-btn
                  icon
                  color="primary"
                  x-large
                  height="64"
                  width="64"
                  title="Wróć do poprzedniego kroku"
                  @click="prevSlide"
                  :disabled="currentSlide == 0"
                >
                  <v-icon>mdi-arrow-left</v-icon>
                </v-btn>
              </div>
              <div class="pr5">
                <span class="f-www f-subheadline fw7" :class="'c-' + (i+1)" v-text="(i < 5) ? i + 1 : 5"></span>
              </div>
              <div class="c-dark flex-grow-1 f-www">
                <div class="f3 fw7 mb2">{{ step.subtitle }}</div>
                <div>{{ step.content }}</div>
              </div>
              <div v-if="currentSlide < 5" class="pt0 pl6">
                <v-hover v-slot="{ hover }">
                  <v-btn
                    :elevation="hover ? 6 : 0"
                    rounded
                    :color="(steps[currentSlide + 1]) ? steps[currentSlide + 1].color : 'primary'"
                    depressed
                    class="xxxl white--text"
                    :disabled="possibleStep <= currentSlide"
                    @click="nextSlide()"
                  >
                    Dalej
                  </v-btn>
                </v-hover>
              </div>
            </div>
          </section>
        </v-carousel-item>
      </v-carousel>
  </div>
  <div v-else class="top-space-half"></div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
export default {
  data: () => ({
    sliderModel: 0
  }),
  computed: {
    ...mapGetters('scenarios', ['id','title','steps','currentStep','currentSlide','possibleStep','editable','loading']),
  },
  watch: {
    currentSlide(slide) {
      if (slide != this.sliderModel) {
        this.sliderModel = slide
      }
    }
  },
  methods: {
    ...mapMutations('scenarios', ['setCurrentStep','setCurrentSlide']),
    prevSlide() {
      let prevSlide = this.currentSlide - 1;

      this.setCurrentSlide(prevSlide);
      if (prevSlide == 4) {
        this.$router.push('/tematy/' + this.id);
      } else if (prevSlide == 5) {
        this.$router.push('/tematy/' + this.id + '/scenariusze');
      }
    },
    nextSlide() {
      let nextSlide = this.currentSlide + 1;
      if (nextSlide == 5) {
        this.$router.push('/tematy/' + this.id + '/scenariusze');
      } else {
        if (nextSlide > this.currentStep) {
          this.setCurrentStep(nextSlide);
        }
        this.setCurrentSlide(nextSlide);
      }
    }
  }
}
</script>