<template>
  <v-app id="app">
    <step-baner />
    <transition
        name="fade"
        mode="out-in"
      >
        <router-view/>
    </transition>
  </v-app>
</template>

<script>
import StepBaner from '@/components/StepBaner.vue';
import { mapGetters, mapActions, mapMutations } from 'vuex'
export default {
  components: {
    StepBaner
  },
  data: () => ({
    
  }),
  computed: {
    ...mapGetters('scenarios', ['id']),
  },
  mounted() {
    var idTopic = document.getElementById('topic').dataset['id'];
    if (idTopic) {
      this.setId(idTopic);
      this.getTopic(idTopic).then(() => {
        this.setup();
      })
    }
  },
  methods: {
    ...mapActions('scenarios', ['setup','getTopic']),
    ...mapMutations('scenarios', ['setId']),
  }
}
</script>

<style>
.fade-enter-active,
.fade-leave-active {
  transition-duration: 0.2s;
  transition-property: opacity;
  transition-timing-function: ease;
}
.fade-enter,
.fade-leave-active {
  opacity: 0
}
