import Vue from 'vue'
import VueRouter from 'vue-router'
import Diagram from '../views/Diagram.vue'
import Scenariusze from '../views/Scenariusze.vue'
import Scenariusz from '../views/Scenariusz.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/tematy/:id',
    name: 'Diagram',
    component: Diagram,
    props: true
  },
  {
    path: '/tematy/:id/scenariusze',
    name: 'Scenariusze',
    component: Scenariusze,
    props: true
  },
  {
    path: '/tematy/:id/scenariusze/:nr',
    name: 'Scenariusz',
    component: Scenariusz,
    props: true
  }
]

const router = new VueRouter({
  mode: 'history',
  routes: routes
})

export default router
