<template>
  <div class="w-100 overflow-hidden">
    <section>
      <div class="section-wrap center ph3 w-100 w-90-l tc">
        <h1 class="c-dark fw7 mv6 f3 f2-m f1-l w-100 w-70-l lh-solid center">{{ title }}</h1>
      </div>
    </section>
    <section v-if="this.loading" class="pv3 tc">
      <v-progress-circular
        :size="566"
        :width="8"
        color="#C9C9C9"
        indeterminate
      ></v-progress-circular>
    </section>
    <section v-else class="diagram">
      <div class="section-wrap center ph3 w-100 w-90-l">
        
        <!-- czynniki kluczowe / sily napedowe -->
        <div class="flex flex-wrap w-100">

          <div class="w-50">
            <div class="w-100 flex flex-wrap">
          
              <div class="w-third flex flex-column justify-center di-le">
                <div class="chip-wrap" v-for="(item, i) in keyFactors" :key="i" :class="[nodeClass(i, keyFactorsLength)]">
                  <v-chip
                    label
                    link
                    :color="steps[1].color"
                    text-color="white"
                    class="tc rounded-lg"
                  >{{ item.title }}</v-chip>
                </div>
              </div>
              <div class="w-2"><!--currentStep > 1--></div>
              <div class="w-third flex items-end">
                <div class="w-100 pv7 relative z-1" :class="{ 'di-step-2': keyFactorsLength > 0 }">
                  <!--select key factors-->
                  <v-dialog
                    v-if="editable"
                    v-model="dialogKey"
                    scrollable
                    persistent
                    max-width="600px"
                  >
                    <template v-slot:activator="{ on: dialogKey }">
                      <div class="relative btc">
                      <div v-if="currentSlide == 1" class="pulse-bg chip2"></div>
                        <v-tooltip top max-width="400" :color="steps[1].color">
                        <template v-slot:activator="{ on: tooltip }">
                          <v-hover v-slot="{ hover }">
                            <v-btn block height="64" 
                              :elevation="hover ? 8 : 0" 
                              :outlined="(currentStep > 0) ? false : true"
                              :color="steps[1].color"
                              :class="{'white--text': currentStep > 0 }"
                              :disabled="currentStep < 1"
                              v-on="{...tooltip, ...dialogKey}"
                              class="rounded-lg"
                            >
                              {{ steps[1].title }}
                            </v-btn>
                          </v-hover>
                        </template>
                        <span v-text="steps[1].content"></span>
                        </v-tooltip>
                      </div>
                    </template>
                    <v-card>
                      <v-card-title>Wybierz 2 czynniki kluczowe</v-card-title>
                      <v-divider></v-divider>
                      
                      <v-card-text class="mt3">
                        <v-chip-group
                          v-model="keyFactorsSelection"
                          column
                          multiple
                          active-class="chip2"
                        >
                          <v-chip
                            v-for="(item, i) in factors" 
                            :key="i" 
                            filter
                            label
                            class="rounded-lg"
                            :value="item.id"
                            :disabled="keyFactorsSelectionLength >= 2 && !keyFactorsSelection.includes(item.id)"
                          >
                            {{ item.title }}
                          </v-chip>
                        </v-chip-group>
                      </v-card-text>

                      <v-divider></v-divider>
                      <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn
                          text
                          @click="dialogKey = false"
                        >
                          Anuluj
                        </v-btn>
                        <v-btn
                          text
                          @click="saveKeyFactors"
                        >
                          Zapisz
                        </v-btn>
                      </v-card-actions>
                    </v-card>
                  </v-dialog>
                  <div v-else class="relative btc">
                    <div v-if="currentSlide == 1" class="pulse-bg chip2"></div>
                    <v-tooltip top max-width="400" :color="steps[1].color">
                    <template v-slot:activator="{ on, attrs }">
                    <v-hover v-slot="{ hover }">
                      <v-btn block height="64" 
                        :elevation="hover ? 8 : 0" 
                        :outlined="(currentStep > 0) ? false : true"
                        :color="steps[1].color"
                        :class="{'white--text': currentStep > 0 }"
                        :disabled="currentStep < 1"
                        v-bind="attrs"
                        v-on="on"
                        class="rounded-lg"
                      >
                        {{ steps[1].title }}
                      </v-btn>
                    </v-hover>
                    </template>
                    <span v-text="steps[1].content"></span>
                    </v-tooltip>
                  </div>

                  <v-btn
                    v-if="editable"
                    fab
                    rounded
                    elevation="0"
                    class="indicator-2"
                    @click="(currentStep > 1) ? setCurrentSlide(1) : null"
                  >2</v-btn>
                  <div class="triangle-down"></div>
                </div>
              </div>
              <div class="w-2"></div>

            </div>
          </div>

          <div class="w-50">
            <div class="w-100 flex flex-wrap">
              <div class="w-2"></div>
              <div class="w-third flex items-end">
                <div class="w-100 pv7 relative z-1" :class="{ 'di-step-3': forcesLength > 0 }">
                  <div class="relative btc">
                    <div v-if="currentSlide == 2" class="pulse-bg chip3"></div>
                    <v-tooltip top max-width="400" :color="steps[2].color">
                    <template v-slot:activator="{ on, attrs }">
                      <v-hover v-slot="{ hover }">
                        <v-btn block height="64" 
                          :elevation="hover ? 8 : 0"
                          :outlined="(currentStep > 1) ? false : true"
                          :color="steps[2].color"
                          :class="{'white--text': currentStep > 1 }"
                          :disabled="currentStep < 2"
                          v-bind="attrs"
                          v-on="on"
                          @click.stop="(editable) ? addFactor(3) : null"
                          class="rounded-lg"
                        >
                          {{ steps[2].title }}
                        </v-btn>
                      </v-hover>
                    </template>
                    <span v-text="steps[2].content"></span>
                    </v-tooltip>
                  </div>
                  <v-btn
                    v-if="editable"
                    fab
                    rounded
                    elevation="0"
                    class="indicator-3"
                    @click="(currentStep > 2) ? setCurrentSlide(2) : null"
                  >3</v-btn>
                  <div class="triangle-down"></div>
                </div>
              </div>
              <div class="w-2"></div>
              <div class="w-third flex flex-column justify-center di-r">
                <div class="chip-wrap"  v-for="(item, i) in forces" :key="i" :class="[nodeClass(i, forcesLength)]">
                  <v-chip
                    label
                    link
                    :color="steps[2].color"
                    text-color="white"
                    class="tc rounded-lg"
                    @click.stop="(editable) ? editFactor(item) : null"
                  >{{ item.title }}</v-chip>
                </div>
              </div>

            </div>
          </div>

        </div>

        <!-- scenariusze -->
        <div class="flex flex-wrap w-100">
          <div class="w-2"></div>
          <div class="w-1"></div>
          <div class="w-2"></div>
          <div class="w-2 relative di-step-5">
            <v-btn
              v-if="editable"
              fab
              rounded
              elevation="0"
              class="indicator-5 z-0"
            >5</v-btn>
            <div class="relative">
              <div v-if="currentSlide == 4" class="pulse-bg chip4"></div>
              <v-hover v-slot="{ hover }">
                <v-btn block height="64"
                  :elevation="hover ? 8 : 0" 
                  :outlined="(currentStep > 2) ? false : true"
                  :color="steps[4].color"
                  :class="{'white--text': currentStep > 2 }"
                  :disabled="currentStep < 3"
                  @click="goToScenarios"
                  class="rounded-lg z1"
                >
                  Scenariusze
                </v-btn>
              </v-hover>
            </div>
          </div>
          <div class="w-2"></div>
          <div class="w-1"></div>
          <div class="w-2"></div>
        </div>

        <!-- czynniki / wildcards -->
        <div class="flex flex-wrap w-100">
          
          <div class="w-50">
            <div class="w-100 flex flex-wrap">
            
              <div class="w-third flex flex-column justify-center di-le relative z-1">
                <div class="chip-wrap" v-for="(item, i) in factors" :key="i" :class="[nodeClass(i, factorsLength)]">
                  <v-chip
                    label
                    link
                    :color="steps[0].color"
                    text-color="white"
                    @click.stop="(editable) ? editFactor(item) : null"
                    class="tc rounded-lg"
                  >{{ item.title }}</v-chip>
                </div>
              </div>

              <div class="w-2"></div>
              <div class="w-third flex items-start">
                <div class="w-100 pv7 relative pulse-container" :class="{ 'di-step-1': factorsLength > 0 }">
                  
                  <div class="relative btc">
                    <div v-if="currentSlide == 0" class="pulse-bg chip1"></div>
                    <v-tooltip top max-width="400" :color="steps[0].color">
                    <template v-slot:activator="{ on, attrs }">
                      <v-hover v-slot="{ hover }">
                        <v-btn block height="64" 
                          :elevation="hover ? 8 : 0"
                          :color="steps[0].color"
                          v-bind="attrs"
                          v-on="on"
                          @click.stop="(editable) ? addFactor(1) : null"
                          class="white--text rounded-lg">
                          {{ steps[0].title }}
                        </v-btn>
                      </v-hover>
                    </template>
                    <span v-text="steps[0].content"></span>
                    </v-tooltip>
                  </div>
                  <v-btn
                    v-if="editable"
                    fab
                    rounded
                    elevation="0"
                    class="indicator-1"
                    @click="setCurrentSlide(0)"
                  >1</v-btn>
                  <div class="triangle-up"></div>
                </div>
              </div>

              <div class="w-2"></div>

            </div>
          </div>


          <div class="w-50">
            <div class="w-100 flex flex-wrap">
              <div class="w-2"></div>

              <div class="w-third flex items-start">
                <div class="w-100 pv7 relative" :class="{ 'di-step-4': wildcardsLength > 0 }">
                  <div class="relative btc">
                    <div v-if="currentSlide == 3" class="pulse-bg chip4"></div>
                      <v-tooltip top max-width="400" :color="steps[3].color">
                      <template v-slot:activator="{ on, attrs }">
                        <v-hover v-slot="{ hover }">
                          <v-btn block height="64" 
                            :elevation="hover ? 8 : 0"
                            :outlined="(currentStep > 2) ? false : true"
                            :color="steps[3].color"
                            :class="{'white--text': currentStep > 2 }"
                            :disabled="currentStep < 3"
                            v-bind="attrs"
                            v-on="on"
                            @click="(editable) ? addFactor(4) : null"
                            class="rounded-lg"
                          >
                            {{ steps[3].title }}
                          </v-btn>
                        </v-hover>
                      </template>
                      <span v-text="steps[3].content"></span>
                      </v-tooltip>
                    </div>
                  <v-btn
                    v-if="editable"
                    fab
                    rounded
                    elevation="0"
                    class="indicator-4"
                    @click="(currentStep > 2) ? setCurrentSlide(3) : null"
                  >4</v-btn>
                  <div class="triangle-up"></div>
                </div>
              </div>

              <div class="w-2"></div>
              
              <div class="w-third flex flex-column justify-center di-r">
                <div class="chip-wrap" v-for="(item, i) in wildcards" :key="i" :class="[nodeClass(i, wildcardsLength)]">
                  <v-chip
                    label
                    link
                    :color="steps[3].color"
                    text-color="white"
                    class="tc rounded-lg"
                    @click.stop="(editable) ? editFactor(item) : null"
                  >{{ item.title }}</v-chip>
                </div>
              </div>

            </div>
          </div>

        </div>

      </div>
    </section>

    <!--edit factor-->
    <v-dialog
      v-model="dialog"
      persistent
      max-width="360"
    >
      <v-card :color="(editedFactor.type) ? steps[editedFactor.type - 1].color : null">
        <v-card-title class="headline">
          
        </v-card-title>

        <v-card-text>
          <v-textarea
            v-model="editedFactor.title"
            name="input-7-1"
            :label="(editedFactor.label) ? editedFactor.label : null"
            rows="3"
            hide-details
            dark
          ></v-textarea>
        </v-card-text>

        <v-card-actions>
          <v-menu
            v-if="editedFactor.id"
            transition="slide-y-transition"
            bottom
            rounded="lg"
            class=""
          >
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                color="white"
                text
                v-bind="attrs"
                v-on="on"
              >
                Usuń
              </v-btn>
            </template>
            <v-list dark color="red">
              <v-list-item @click="delFactor">
                <v-list-item-title>Potwierdzam usunięcie</v-list-item-title>
              </v-list-item>
            </v-list>
          </v-menu>
          <v-spacer></v-spacer>

          <v-btn
            color="white"
            text
            @click="dialog = false"
          >
            Anuluj
          </v-btn>

          <v-btn
            color="white"
            text
            @click="insertFactor"
          >
            Zapisz
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

  </div>
</template>

<script>
// @ is an alias to /src
//import HelloWorld from '@/components/HelloWorld.vue'
import { mapGetters, mapActions, mapMutations } from 'vuex'

export default {
    data: () => ({
      dialog: false,
      dialogKey: false,
      keyFactorsSelection: [],
      dialogm1: '',
      editedFactor: {
        id: '',
        id_topic: '',
        title: '',
        ord: '',
        type: ''
      },
    }),
    computed: {
      ...mapGetters('scenarios', ['id','currentStep', 'currentSlide', 'factorsByType','factors', 'factorsLength', 'keyFactors', 'forces','forcesLength','wildcards','wildcardsLength','keyFactorsLength', 'title', 'steps', 'editable', 'loading']),
      keyFactorsSelectionLength() {
        return this.keyFactorsSelection.length
      },
    },
    watch: {
      dialogKey(opened) {
        if (opened) {
          this.keyFactorsSelection = this.keyFactors.map(el => el.id);
        }
      }
    },
    methods: {
      ...mapActions('scenarios', ['getTopic','updateFactor','updateKeyFactors','deleteFactor']),
      ...mapMutations('scenarios', ['setCurrentSlide']),
      nodeClass(i, all) {
        if (i == 0 && all == 1) {
          return { 'di-single': true }
        } else if (i == 0) {
          return { 'di-first': true }
        } else if (i == all - 1) {
          return { 'di-last': true }
        } else {
          return { 'di-mid': true }
        }
        
      },
      addFactor(type) {
        var labels = {
          '1': 'Dodaj czynnik',
          '3': 'Dodaj siłę napędową',
          '4': 'Dodaj wild card'
        };
        
        this.editedFactor = {
          id: '',
          id_topic: this.id,
          title: '',
          type: type,
          key_factor: '0',
          ord: '0',
          label: labels[type]
        }
        this.dialog = true;
      },
      editFactor(factor) {
        var label = 'Czynnik';
        this.editedFactor = {...factor};
        this.editedFactor.label = label;
        this.dialog = true;
      },
      insertFactor() {
        this.updateFactor(this.editedFactor)
        .then(() => this.dialog = false );
      },
      delFactor() {
        this.deleteFactor(this.editedFactor)
        .then(() => this.dialog = false );
      },
      saveKeyFactors() {
        //keyFactorsSelection
        //let selectedIds = this.keyFactorsSelection.map(el => el.id);
        
        this.updateKeyFactors({
          id_topic: this.id,
          keyFactorsId: this.keyFactorsSelection //.map(el => el.id)
        });
        
        this.dialogKey = false;
      },
      goToScenarios() {
        this.$router.push('/tematy/' + this.id + '/scenariusze');
      }
    }
  }
</script>

<style>
.btc {
  min-height: 64px;
  width: 100%;
}
.chip-wrap .v-chip--clickable {
  will-change: box-shadow;
  transition-duration: 0.28s;
  transition-property: box-shadow, transform, opacity;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.chip-wrap .v-chip--clickable:hover {
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2), 0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12) !important;
}
/*pulse*/
.pulse-bg {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 15px;
}
.pulse-bg {
  animation: pulse 1.2s ease infinite;
  background: #ffd332;
}
@keyframes pulse {
  0% {
    transform: scale(1, 1);
  }
  50% {
    opacity: 0.3;
  }
  100% {
    transform: scale(1.5);
    opacity: 0;
  }
}
</style>