//import { getToken } from "./getToken";
import { badResponse } from "./badResponse";

//const apiUrl = 'http://localhost/api'; //dev
//const apiUrl = 'http://www.solarcloud.pl/api'; //prod
const apiUrl = '/api'; //prod

export default {
  getTopic(id) {
    return fetch(apiUrl + '/topic/' + id + '?t=' + new Date().getTime(), {
      method: 'GET'
    })
    .then(response => response.json())
    .then(data => {
      return data;
    })
    .catch(() => badResponse());
  },
  updateFactor(factor) {
    const formData = new FormData();
    formData.append('id', factor.id);
    formData.append('id_topic', factor.id_topic);
    formData.append('title', factor.title);
    formData.append('type', factor.type);
    formData.append('key_factor', factor.key_factor);
    formData.append('ord', factor.ord);
    
    return fetch(apiUrl + '/update_factor?t=' + new Date().getTime(), {
      method: 'POST',
      body: formData
    })
    .then(response => response.json())
    .catch(() => badResponse());
  },
  deleteFactor(factor) {
    const formData = new FormData();
    formData.append('id', factor.id);
    formData.append('id_topic', factor.id_topic);
    
    return fetch(apiUrl + '/delete_factor?t=' + new Date().getTime(), {
      method: 'POST',
      body: formData
    })
    .then(response => response.json())
    .catch(() => badResponse());
  },
  updateKeyFactors({id_topic, keyFactorsId}) {
    const formData = new FormData();
    formData.append('id_topic', id_topic);

    keyFactorsId.forEach((id) => {
      return formData.append('id[]', id);
    });
    
    return fetch(apiUrl + '/update_key_factors?t=' + new Date().getTime(), {
      method: 'POST',
      body: formData
    })
    .then(response => response.json())
    .catch(() => badResponse());
  },
  updateScenario(data) {
    const formData = new FormData();
    formData.append('id', data.id);
    formData.append('id_topic', data.id_topic);
    formData.append('title', data.title);
    formData.append('content', data.content);
    formData.append('ord', data.ord);
    formData.append('factors', data.factors);
    
    return fetch(apiUrl + '/update_scenario?t=' + new Date().getTime(), {
      method: 'POST',
      body: formData
    })
    .then(response => response.json())
    .catch(() => badResponse());
  },
}
